import React from 'react'
import './Product.css'

const Product = (props) => {
    return (
        <div className="product">
            <div className="productImg"
                style={{ backgroundImage: "url(" + props.product.photoSrc + ")" }}
                onClick={() => {
                    props.setProductToViewer(props.product)
                    props.setShowingViewer()
                }}
            >
            </div>
            <div className="productDescription" onClick={() => {
                props.setProductToViewer(props.product)
                props.setShowingViewer()
            }}>
                <p className="productTextStyle">{props.product.brand} {props.product.model}</p>
                <p className="productTextStyle">{props.product.cost + '$'}</p>
            </div>
            <button onClick={() => props.addProductToBasket(props.product)}
                className="inBasket"
                type="button">
                <p className="productTextStyle">Add to basket</p>
            </button>
        </div>
    )
}

export default Product