import { ADD_PRODUCT_TO_BASKET, DELETE_PRODUCT_FROM_BASKET, RESET_BASKET } from "./types"

let initialState = {
    basket: [],
    totalCost: 0
}

const basketReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_PRODUCT_TO_BASKET:
            return {
                ...state,
                basket: [...state.basket, action.product],
                totalCost: state.totalCost + action.product.cost
            }
        case DELETE_PRODUCT_FROM_BASKET: 
            return {
                ...state,
                basket: [...state.basket.filter(product => product.id !== action.id)],
                totalCost: state.totalCost - action.cost
            }
        case RESET_BASKET:
            return {
                ...state,
                basket: [],
                totalCost: 0
            }
        default: return state
    }
}

export const addProductToBasket = (product) => {
    return {
        type: ADD_PRODUCT_TO_BASKET, product
    }
}

export const deleteProductFromBasket = (id, cost) => {
    return {
        type: DELETE_PRODUCT_FROM_BASKET, id, cost
    }
}

export const resetBasket = () => {
    return {
        type: RESET_BASKET
    }
}

export default basketReducer
