import { PRODUCT_FILTER, SET_VIEWER } from "./types"

let initialState = {
    manProducts: [
        { id: 1, brand: "Adidas", model: "Yeezy Boost", cost: 100, photoSrc: "./img/adidasYeezyBoost.jpg", color: "Grey" },
        { id: 2, brand: "Nike", model: "Air Force", cost: 250, photoSrc: "./img/nikeAirForce.jpg", color: "White"},
        { id: 3, brand: "Nike", model: "Air Jordan", cost: 400, photoSrc: "./img/nikeAirJordan.jpg", color: "Red" },
        { id: 4, brand: "Nike", model: "Air Max 270", cost: 220, photoSrc: "./img/nikeAirMax270.jpg", color: "Black" },
        { id: 5, brand: "Nike", model: "Cortez", cost: 580, photoSrc: "./img/nikeCortez.jpg", color: "White" },
        { id: 6, brand: "Reebok", model: "Classics", cost: 280, photoSrc: "./img/reebokClassics.jpg", color: "White" },
        { id: 7, brand: "Reebok", model: "Daytona DMX", cost: 320, photoSrc: "./img/reebokDMX.jpg", color: "White" },
        { id: 8, brand: "Reebok", model: "Kamikaze", cost: 460, photoSrc: "./img/reebokKamikaze.jpg", color: "Black" }
    ],
    womanProducts: [
        { id: 1, brand: "Adidas Woman", model: "Yeezy Boost", cost: 100, photoSrc: "./img/adidasYeezyBoost.jpg", color: "Grey" },
        { id: 2, brand: "Nike", model: "Air Force", cost: 250, photoSrc: "./img/nikeAirForce.jpg", color: "White"},
        { id: 3, brand: "Nike", model: "Air Jordan", cost: 400, photoSrc: "./img/nikeAirJordan.jpg", color: "Red" },
        { id: 4, brand: "Nike", model: "Air Max 270", cost: 220, photoSrc: "./img/nikeAirMax270.jpg", color: "Black" },
        { id: 5, brand: "Nike", model: "Cortez", cost: 580, photoSrc: "./img/nikeCortez.jpg", color: "White" },
        { id: 6, brand: "Reebok", model: "Classics", cost: 280, photoSrc: "./img/reebokClassics.jpg", color: "White" },
        { id: 7, brand: "Reebok", model: "Daytona DMX", cost: 320, photoSrc: "./img/reebokDMX.jpg", color: "White" },
        { id: 8, brand: "Reebok", model: "Kamikaze", cost: 460, photoSrc: "./img/reebokKamikaze.jpg", color: "Black" }
    ],
    productViewer: {
        id: null,
        photoSrc: null,
        brand: null,
        model: null,
        cost: null
    },
    requiredCatalog: []
}

const catalogReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_VIEWER:
            console.log(action)
            return {
                ...state,
                productViewer: action.product
            }
        case PRODUCT_FILTER:
            let newCatalog
            if(action.currentCatalog === "manProducts") {
                newCatalog = state.manProducts.map(item => {
                    return {...item}
                })
            }
            if(action.currentCatalog === "womanProducts") {
                newCatalog = state.womanProducts.map(item => {
                    return {...item}
                })
            }
            if(action.brand && action.brand !== "Select brand") { 
                newCatalog = newCatalog.filter(item => item.brand === action.brand)
            }
            if(action.color && action.color !== "Select color") {
                newCatalog = newCatalog.filter(item => item.color === action.color)
            }
            if(action.cost) {
                let costFilter = Number(action.cost)
                console.log(costFilter)
                newCatalog = newCatalog.filter(item => item.cost < costFilter )
            }
            if(action.sort && action.sort !== "Sort") {
                if(action.sort && action.sort !== "Sort") {
                    if(action.sort === "Price high to low") {
                    newCatalog.sort((prev , next) => {
                        return next.cost - prev.cost
                    })
                    }
                    if(action.sort === "Price low to high") {
                        newCatalog.sort((prev , next) => {
                            return prev.cost - next.cost
                        })
                    }
                }
            }
            return {
                ...state, requiredCatalog: newCatalog
            }
            default: return state
    }
}

export const setProductToViewer = (product) => {
    return { type: SET_VIEWER, product}
}

export const productFilter = ({brand = null, color = null, cost = null, sort = null}, currentCatalog) => {
    console.log(currentCatalog)
    return { type: PRODUCT_FILTER, brand, color, cost, sort, currentCatalog}
}

export default catalogReducer