import React from 'react'
import './OrderProduct.css'

const OrderProduct = (props) => {
    return (
        <div className="orderProduct">
            <div className="orderProductImg" 
                 style={{ backgroundImage: "url(" + props.product.photoSrc + ")" }}>
            </div>
            <div className="orderProductDescription">
                <p className="orderProductName">{props.product.brand} {props.product.model}</p>
                <p className="orderProductCost">{props.product.cost}$</p>
            </div>
        </div>
    )
}

export default OrderProduct