import React from 'react'
import './ProductViewer.css'

class ProductViewer extends React.Component {
    componentDidMount() {
        window.scroll(0, 0)
    }
    render() {
        return (
            <div>
                <div className="productViewerWrap">
                    <div className="productViewerImgWrap" style={{ backgroundImage: "url(" + this.props.product.photoSrc + ")" }}>
                    </div>
                    <div className="productViewerInfo">
                        <div className="productViewerExitWrap" onClick={() => this.props.setShowingViewer()}>
                            <span style={{
                                backgroundImage: "url(./img/x.png)"
                            }}
                                className="productViewerExit"></span>
                        </div>
                        <p className="productViewerText">{this.props.product.brand} {this.props.product.model}</p>
                        <p className="productViewerText">{this.props.product.cost}$</p>
                        <button className="productViewerButton" onClick={() => this.props.addProductToBasket(this.props.product)}>Add to Basket</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default ProductViewer