import React from 'react'
import { connect } from 'react-redux'
import { Field, reduxForm } from 'redux-form'
import './ProductFilter.css'

const FilterForm = props => {
    const onReset = (dispatch) => {
        props.reset()
        props.setFilteredCatalog(false)
    }
    return (
        <form onSubmit={props.handleSubmit}>
            <div className="filterFlexWrap">
                <div>
                    <Field
                        name="type"
                        component="select"
                    >
                        <option>Select type</option>
                        <option>Sneakers</option>
                        <option>Boots</option>
                    </Field>
                </div>
                <div>
                    <Field
                        name="brand"
                        component="select"
                    >
                        <option>Select brand</option>
                        <option>Nike</option>
                        <option>Adidas</option>
                        <option>Reebok</option>
                    </Field>
                </div>
                <div>
                    <Field
                        name="color"
                        component="select"
                    >
                        <option>Select color</option>
                        <option>White</option>
                        <option>Black</option>
                        <option>Red</option>
                        <option>Grey</option>
                    </Field>
                </div>
                <div>
                    <Field
                        name="cost"
                        type="range"
                        component="input"
                        min="100"
                        max="1000"
                    >
                    </Field>
                </div>
                <div>
                    <Field
                        name="sort"
                        component="select"
                    >
                        <option>Sort</option>
                        <option>Price high to low</option>
                        <option>Price low to high</option>
                    </Field>
                </div>
            </div>
            <div className="filterButtonWrap">
                <button type="submit" className="filterButton">Filter</button>
                <button type="button" className="filterReset" onClick={() => onReset()}>Reset filter</button>
            </div>

        </form>
    )
}

const ReduxFilterForm = reduxForm({ form: 'filter' })(FilterForm)

const ProductFilter = (props) => {
    const onSubmit = (formdata) => {
        props.productFilter(formdata, props.currentCatalog)
        props.setFilteredCatalog(true)
    }
    return (
        <div className="filterWrap">
            <ReduxFilterForm onSubmit={onSubmit} setFilteredCatalog={props.setFilteredCatalog} />
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        filters: state.catalog.filters
    }
}

export default connect(mapStateToProps, null)(ProductFilter)