import React from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { deleteProductFromBasket } from '../../redux/basketReducer'
import './Basket.css'
import BasketItem from './basketItem/BasketItem'

const Basket = (props) => {
    return (
        <div className="basket" >
            <div className="basketExitWrap" onClick={() => props.setShowingBasket(false)}>
                <span style={{
                    backgroundImage: "url(./img/x.png)"
                  }} 
                      className="basketExit"></span>
            </div>
            <div className="basketContent">
                {props.basket.length > 0 ? props.basket.map(item =>
                    <BasketItem
                        item={item}
                        deleteProductFromBasket={props.deleteProductFromBasket}
                        key={item.id}
                    />

                )
                    :
                    <div>You didn't add products to basket</div>

                }
            </div>
            <div className="basketInfo">
                    <div className="costInfo">
                        <p className="costInfoText">Order sum: {props.totalCost}$</p>
                    </div>
                    <NavLink to="/order">
                    <button disabled={props.totalCost === 0} onClick={() => props.setShowingBasket(false)} className="makeOrderButton">Order</button>
                    </NavLink>
                </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        basket: state.basket.basket,
        totalCost: state.basket.totalCost
    }
}

export default connect(mapStateToProps, { deleteProductFromBasket })(Basket)