import React from 'react'
import './BasketItem.css'

class BasketItem extends React.Component {
    componentDidMount() {
        window.scroll(0, 0)
    }
    render() {
        return (
            <div className="basketItem">
                <div>
                    <img className="basketItemImg" alt="" src={this.props.item.photoSrc} />
                    <p className="basketItemName">{this.props.item.brand} {this.props.item.model}</p>
                    <p className="basketItemCost">{this.props.item.cost}$</p>
                </div>
                <span style={{
                    backgroundImage: "url(./img/x.png)"
                }}
                    onClick={() => this.props.deleteProductFromBasket(this.props.item.id, this.props.item.cost)}></span>
            </div>
        )
    }
}

export default BasketItem