import React from 'react'
import './ProductList.css'
import Product from './product/Product'
import { addProductToBasket } from '../../redux/basketReducer'
import { setProductToViewer, productFilter } from '../../redux/catalogReducer'
import { connect } from 'react-redux'
import ProductFilter from './productFilter/ProductFilter'
import ProductViewer from './productViewer/ProductViewer'

class ProductList extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            isShowingViewer: false,
            productList: this.props.catalog,
            filterMode : false
        }
        this.setShowingViewer = this.setShowingViewer.bind(this)
        this.setFilteredCatalog = this.setFilteredCatalog.bind(this)
    }
    componentDidMount() {
        window.scroll(0, 0)
    }
    setShowingViewer() {
        this.setState((state) => {
            return { isShowingViewer: !state.isShowingViewer }
        })
    }

    setFilteredCatalog (value) {
        this.setState({filterMode: value})
    }

    render() {
        return (
            this.state.isShowingViewer ?
                <ProductViewer product={this.props.productViewer}
                    addProductToBasket={this.props.addProductToBasket}
                    setShowingViewer={this.setShowingViewer}
                />
                :
                <>
                    <ProductFilter setFilteredCatalog={this.setFilteredCatalog}
                                   productFilter={this.props.productFilter}
                                   currentCatalog={this.props.currentCatalog === "man" ? 
                                    "manProducts" : "womanProducts"}
                    />
                    <div className="productList">
                        {this.state.filterMode ?
                        this.props.requiredCatalog.map(product =>
                            <Product key={product.id}
                                product={product}
                                addProductToBasket={this.props.addProductToBasket}
                                setShowingViewer={this.setShowingViewer}
                                setProductToViewer={this.props.setProductToViewer}
                            />
                        )
                        :
                        this.state.productList.map(product =>
                            <Product key={product.id}
                                product={product}
                                addProductToBasket={this.props.addProductToBasket}
                                setShowingViewer={this.setShowingViewer}
                                setProductToViewer={this.props.setProductToViewer}
                            />
                        )}
                    </div>
                </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        productViewer: state.catalog.productViewer,
        requiredCatalog: state.catalog.requiredCatalog
    }
}

export default connect(mapStateToProps, { addProductToBasket, setProductToViewer, productFilter })(ProductList) 