import React from 'react'
import './SexCategories.css'
import WomanCategory from './woman/WomanCategory'
import ManCategory from './manCategory/ManCategory'
import { NavLink } from 'react-router-dom'

const SexCategories = () => {
    return (
        <div className="sexCategories">
            <NavLink to="/woman_catalog"><WomanCategory /></NavLink>
            <NavLink to="/man_catalog"><ManCategory /></NavLink>
        </div>
    )
}

export default SexCategories