import React from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { Field, reduxForm } from 'redux-form'
import './Order.css'
import OrderProduct from './OrderProduct'
import { resetBasket } from '../../redux/basketReducer'

const OrderForm = (props) => {
    return (
        <form className="orderForm" onSubmit={props.onSubmit}>
            <h2>Client information</h2>
            <Field name="email"
                type="text"
                component="input"
                placeholder="Enter your e-mail"
            ></Field>
            <Field name="phoneNumber"
                type="text"
                placeholder="Enter your phone number"
                component="input"
            ></Field>
            <Field name="address"
                type="text"
                placeholder="Enter your address"
                component="input"
            ></Field>
            <Field name="paying"
                type="radio"
                component="select"
            >
                <option>Paying by</option>
                <option>Non-cash</option>
                <option>On delivery</option>
            </Field>
            <NavLink className="orderButtonWrap" onClick={() => props.resetBasket()} to="/">
                <button>Confirm order</button>
            </NavLink>
        </form>
    )
}

const ReduxOrderForm = reduxForm({ form: "order" })(OrderForm)

class Order extends React.Component {
    componentDidMount() {
        window.scroll(0, 0)
    }
    constructor(props) {
        super(props)
        this.onSubmit = this.onSubmit.bind(this)
    }
    onSubmit = () => { }
    render() {
        return (
            <>
                <div className="orderWrap">
                    <div className="orderFormWrap orderSection">
                        <ReduxOrderForm onSubmit={this.onSubmit} resetBasket={this.props.resetBasket} />
                    </div>
                    <div className="orderInfo orderSection">
                        <p className="orderInfoTitle">Products in the order</p>
                        <div className="orderProducts">
                            {this.props.order.map(product =>
                                <OrderProduct product={product} key={product.id} />
                            )}
                        </div>
                        <p className="orderInfoSum">Order sum: {this.props.totalCost}$</p>
                    </div>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        order: state.basket.basket,
        totalCost: state.basket.totalCost
    }
}

export default connect(mapStateToProps, { resetBasket })(Order)