import React, { useState } from 'react'
import { Route } from 'react-router-dom'
import './App.css'
import Header from './components/header/Header'
import Banner from './components/banner/Banner'
import SexCategories from './components/sexCategories/SexCategories'
import History from './components/history/History'
import Footer from './components/footer/Footer'
import ProductList from './components/productList/ProductList'
import Basket from './components/basket/Basket'
import { connect } from 'react-redux'
import Order from './components/order/Order'

const App = (props) => {
  const [isShowingBasket, setShowing] = useState(false)
  const setShowingBasket = (value) => {
    setShowing(value)
  }
  return (
    <div className="App">
      <Header setShowingBasket={setShowingBasket}
        isShowingBasket={isShowingBasket}
      />
      {isShowingBasket ?
        <div className="appContent">
          <Basket setShowingBasket={setShowingBasket} />
        </div>
        :
        <div className="appContent">
          <Route exact path="/" render={() =>
            <div>
              <Banner />
              <SexCategories />
              <History />
            </div>
          } />
          <Route path="/man_catalog" render={() =>
            <ProductList catalog={props.manCatalog} currentCatalog="man" />
          } />
          <Route path="/woman_catalog" render={() =>
            <ProductList catalog={props.womanCatalog} currentCatalog="woman" />
          } />
          <Route path="/order" render={() =>
            <Order />
          } />
        </div>
      }
      <Footer />
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    manCatalog: state.catalog.manProducts,
    womanCatalog: state.catalog.womanProducts
  }
}

export default connect(mapStateToProps, null)(App)
