import React from 'react'
import './Header.css'
import { NavLink } from 'react-router-dom'
import { connect } from 'react-redux'

const Header = (props) => {
    return (
        <header>
            <NavLink to="/">
                <div className="logoContainer">
                    <p className="logo" onClick={() => props.setShowingBasket(false)}>SNEAKERS STORE</p>
                </div>
            </NavLink>
            {props.isShowingBasket ?
                null
                :
                <div className="basketButton" onClick={() => props.setShowingBasket(true)}>
                    <img className="basketLogo" alt="" src="./img/basketLogo.png" />
                    <p className="basketCounter">{props.basket}</p>
                </div>
            }
        </header>
    )
}

const mapStateToProps = (state) => {
    return {
        basket: state.basket.basket.length
    }
}

export default connect(mapStateToProps, null)(Header)